$(document).ready(function(){

	for (var i = 0; i < $('.nav-item').length; i++){

		$('.nav-item').eq(i).on('click', (function(i){
			return function(){
				if (!$(this).hasClass('focus')){
					$('.nav-item').removeClass('focus');
					$(this).addClass('focus');
				}

				$('.content').hide();
				$('.content').eq(i).show();
			}
		})(i))
	}

})


